import React from "react";
import Product from "./Product.js";
import productData from "../product.js";
console.log(productData)
function Main(){
    return(
        <div className="Main_productContainer">
            { 
                productData.map((myproduct)=>{
                    return (
                        <Product productName={myproduct.productName} 
                    productPrice={myproduct.productPrice} ratingCount={myproduct.ratingCount}
                    ImgSrc={myproduct.ImgSrc}/>)

                }
                )
        }

            
            
            

        </div>
    )
}
export default Main;
